<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="6">
        <h2> </h2>
        <v-text-field disabled v-model="userInfo.uName" label=""></v-text-field>
        <v-text-field disabled v-model="userInfo.sex" label=""></v-text-field>
        <v-text-field v-model="userInfo.age" label=""></v-text-field>
      </v-col>
      <v-col cols="12" sm="6">
        <h2></h2>
        <v-text-field v-model="userInfo.height" label=""></v-text-field>
        <v-text-field v-model="userInfo.weight" label=""></v-text-field>
        <h2> </h2>
        <v-radio-group v-model="userInfo.weak">
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
        </v-radio-group>
      </v-col>
      <v-divider vertical></v-divider>
      <v-col cols="12" sm="6">
        <h2>1RM</h2>
        <v-text-field v-model="userInfo.squat" label=""></v-text-field>
        <v-text-field v-model="userInfo.bench" label=""></v-text-field>
        <v-text-field v-model="userInfo.dead" label=""></v-text-field>
        <h2></h2>
        <v-radio-group mandatory v-model="userInfo.proficiency">
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
          <v-radio label="" value=""></v-radio>
        </v-radio-group>
      </v-col>
    </v-row>
    <v-btn @click="test"></v-btn>
  </v-container>
</template>
<script>
import VueRouter from '@/router/index.js';
export default {
  created() {
    var id = localStorage.getItem('id');
    console.log(id);
    this.userInfo.uName = localStorage.getItem('uName');
    this.userInfo.email = localStorage.getItem('email');
    this.userInfo.sex = localStorage.getItem('sex');
    if (localStorage.getItem('sex') == 1) {
      this.userInfo.sex = '';
    } else if (localStorage.getItem('sex') == 2) {
      this.userInfo.sex = '';
    }
    fetch(`http://115.85.183.157:3000/register/userInfo/getUID/${id}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(res => res.json())
      .then(res => {
        var uID = res.uID;
        this.userInfo.uID = uID;
        console.log(uID);
      })
      .catch(err => {
        console.error('   ');
      });

    console.log(this.userInfo);
  },
  data() {
    return {
      userInfo: {
        uID: 0,
        uName: '',
        age: '',
        email: '',
        sex: '',
        height: '',
        weight: '',
        squat: '',
        bench: '',
        dead: '',
        weak: '',
        proficiency: '',
      },
    };
  },
  methods: {
    test() {
      this.saveUser();
    },
    saveUser() {
      if (this.userInfo.proficiency === '') {
        this.userInfo.proficiency = 1;
      } else if (this.userInfo.proficiency === '') {
        this.userInfo.proficiency = 2;
      } else if (this.userInfo.proficiency === '') {
        this.userInfo.proficiency = 3;
      }
      const req = this.userInfo;
      fetch('http://115.85.183.157:3000/register/userInfo', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(req),
      })
        .then(res => res.json())
        .then(res => {
          if (res.success) {
            alert('');
            localStorage.clear();
            VueRouter.push({ path: '/' });
          }
        })
        .catch(err => {
          console.error('   ');
        });
      VueRouter.push({ path: '/' });
    },
  },
};
</script>
