function getSets(nextTarget, targetIdx) {
  console.log('here is getSets');
  console.log(user.userInfo);
  // console.log(nextTarget, targetIdx);
  let sets = [];
  const max_set = 5;
  let weight = 0;
  let reps = 0;
  let oneRM = 0;

  for (var i = 0; i < max_set; i++) {
    switch (nextTarget) {
      case '':
        oneRM = userInfo.squat;

        switch (targetIdx) {
          case 0:
          case 1:
          case 2:
          case 3:
          case 8:
            weight = oneRM;
            if (i < 2) weight = weight * (0.5 + 0.2 * i);
            else if (i < 4) weight = weight * 0.8;
            else weight = weight * 0.7;
            break;
          case 6:
          case 7:
          case 10:
          case 11:
            weight = oneRM * 0.7;
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          case 4:
          case 5:
          case 9:
          case 12:
            weight = oneRM / 2;
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          default:
        }
        break;
      case '':
        oneRM = userInfo.bench;

        switch (targetIdx) {
          case 0:
            weight = oneRM;
            if (i < 2) weight = weight * (0.5 + 0.2 * i);
            else if (i < 4) weight = weight * 0.8;
            else weight = weight * 0.7;
            break;
          case 1:
          case 2:
          case 3:
          case 4:
          case 5:
          case 6:
          case 9:
          case 10:
            weight = oneRM * 0.7;
            if (targetIdx == 4 || targetIdx == 5) weight = weight / 2;
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          case 8:
          case 11:
            weight = oneRM * 0.4;
            if (targetIdx == 4 || targetIdx == 5) weight = weight / 2;
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          case 7:
            if (oneRM > 100) weight = 10;
            else weight = 5;
        }
        break;
      case '':
        oneRM = userInfo.dead;
        switch (targetIdx) {
          case 0:
          case 6:
            weight = oneRM;
            if (i < 2) weight = weight * (0.5 + 0.2 * i);
            else if (i < 4) weight = weight * 0.8;
            else weight = weight * 0.7;
            break;
          case 1:
          case 3:
          case 4:
          case 5:
          case 7:
          case 8:
          case 9:
          case 10:
            weight = oneRM * 0.7;
            if (targetIdx == 4 || targetIdx == 5) weight = weight / 2;
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
        }
        break;
      case '':
        weight = getShoulderWeight();
        console.log(`  : ${weight}`);
        switch (targetIdx) {
          case 0:
            // 
            if (i < 2) weight = weight * (0.7 + 0.1 * i);
            else if (i < 4) weight = weight * 0.8;
            else weight = weight * 0.7;
            break;

          case 1:
          case 2:
          case 3:
          case 7:
            // 
            if (i < 2) weight = weight * (0.5 + 0.2 * i);
            else if (i < 4) weight = weight * 0.8;
            else weight = weight * 0.7;
            break;
          case 4:
          case 5:
          case 6:
          case 8:
          case 9:
            // 
            if (i < 3) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
        }
        break;
      case '':
        weight = getArmWeight();
        switch (targetIdx) {
          case 0:
          case 6:
            // 
            if (i < 2) weight = weight * (0.7 + 0.2 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          case 1:
          case 3:
          case 4:
          case 5:
            // 
            weight = weight * 2;
            if (i < 2) weight = weight * (0.7 + 0.1 * i);
            else if (i == 4) weight = weight * 0.8;
            break;
          case 2:
            // 
            weight = weight * 6;
            if (i < 2) weight = weight * (0.7 + 0.1 * i);
            else if (i < 4) weight = weight * 0.85;
            else weight = weight * 0.8;
            break;
        }
        console.log(weight);
    }
    if (i < 3) reps = 10 - 2 * i;
    if (i == 4) reps = 7;
    weight = parseInt(weight);
    sets.push({ reps, weight });
  }
  console.log('here is end of getSets - sets');
  return sets;
}

